import React,{useState} from 'react';
import SectionCardGrid from './section.card.grid';
import SectionChat from './section.chat';
import'../../css/targetas.css';
const SectionCardproject = (props)=>{
  const [visible,setVisible]=useState(false)
  const handleModal =(visibilidad)=>{
    setVisible(visibilidad)
  }
    return (
      <>
        <div className="card _card" style={{border:"2px solid  #00d1b2"}}>
    
        <div className="card-image" style={{overflow:"hidden"}}>
          <figure className="image is-4by3">
            <img src={props.data.img} alt={props.data.nombre}/>
          </figure>
        </div>
        <div className="card-content">
          <div className="media">
            <div className="media-left">
              <figure className="image is-48x48 _anime _card" style={{borderRadius:"100%",borderTop:"1px solid  #00d1b2",borderBottom:" 1px solid #3b055e"}}>
                <a href={props.data.url} target="_blank" rel="noopener">
                <img src="https://img.icons8.com/fluent/48/000000/github.png" alt="Placeholder image"/>
                </a>
              </figure>
            </div>
            <div className="media-content">
              <p className="title is-4" style={{fontSize:"2.7vh"}}>{props.data.nombre}</p>
              <p className="subtitle is-6">{props.data.titulo}</p>
            </div>
          </div>
          <SectionCardGrid grid={[]}/>
          <div className="content">
            {props.data.descripccion}
            <br/>
            <time datetime={props.data.fecha}>{props.data.fecha}
            {'\u00A0'}{'\u00A0'}{'\u00A0'}{'\u00A0'}{'\u00A0'}{'\u00A0'}{'\u00A0'}{'\u00A0'}
            <div className="control" style={{display:"inline-block"}}>
                <div className="tags has-addons" onClick={()=>{handleModal(true)}}>
                    <span className="tag is-dark">Contacto</span>
                    <span className="tag is-primary">Click +</span>
                </div>
            </div>
            </time>
          </div>
        </div>
      </div>
      <SectionChat visible={visible} func={handleModal}/>
      </>
    )
}
export default SectionCardproject;